.footer-container {
    position: relative;
    width: 100%;
    height: 200px;
    background: black;
    display: flex;
    flex-wrap: wrap;
}

.footer-container .col-footer {
    position: relative;
    width: 33.333333%;
    justify-content: center;
    align-items: center;
}
.footer-container .col-footer h2 {
    color: white;
    font-weight: 800;
    padding: 25px;
    font-size: 12pt;
    padding: 20px 50px;
}
.footer-container .col-footer p {
    width: 80%;
    padding: 0px 50px;
    font-size: 10pt;
}

.sosmed {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.sosmed a {
    width: 20%;
}
#icon {
    width: 50%;
}